.header {
  //Hamburger open styles
  &.open {
    .header__toggle {
      > span:first-child {
        transform: rotate(40deg);
      }
      > span:nth-child(2) {
        opacity: 0;
      }
      > span:last-child {
        transform: rotate(-40deg);
      }
    }
  }

  //hamburger Open styles
  .overlay {
    visibility: hidden;
    opacity: 0;
    position: fixed;
    top: 3.75rem;
    right: 0px;
    bottom: 0px;
    left: 0px;
    background-image: linear-gradient($darkBlue, transparent);
  }

  nav {
    position: relative;
    background-color: $white;
    padding-top: 1.0625rem;
    padding-bottom: 1.0625rem;
  }

  &__logo {
    img {
      width: 8.8125rem;
      height: 1.375rem;
    }
  }
  &__toggle {
    //Mobile Menu
    > span {
      display: block;
      width: 26px;
      height: 2px;
      background-color: $darkBlue;
      transition: all 300ms ease-in-out;
    transform-origin: 3px 1px;

      &:not(:last-child) {
        margin-bottom: 5px;
      }
    }
  }

  &__menu {
    position: absolute;
    width: calc(100% - 3rem);
    left: 50%;
    transform: translateX(-50%);
    background-color: $white;
    margin-top: 3rem;
    padding: 1.5rem;
    border-radius: 5px;
    a {
      display: block;
      padding: 0.625rem;
      text-align: center;
      color: $darkBlue;
    }
  }

  &__links {
    a {
      position: relative;
      font-size: $font-small;
      color: $grayisBlue;
      transition: color 300ms ease-in-out;

      &:not(:last-child) {
        margin-right: 24px;
      }

      &::before {
        position: absolute;
        display: block;
        content: "";
        height: 5px;
        left: 0;
        right: 0;
        bottom: -30px;
        background: linear-gradient(to right, $limeGreen, $brightCyan);
        opacity: 0;
        transition: opacity 300ms ease-in-out;
      }

      &:hover {
        color: $darkBlue;
        &::before {
          opacity: 1;
        }
      }
    }
  }
}
